import {mapGetters, mapState} from "vuex";

export default {
    name: 'search',

    data() {
        return {
            list: [],
            keyword: '',
            activity: {},
            historyList: [],
            isSearch: false,
            page: 1
        }
    },

    onLoad() { this.$commonLoad.onload();
        this.historyList = this.getHistory();
    },
    computed: {
        ...mapState({
            appImg: state => state.mallConfig.__wxapp_img.mall,
            appSetting: state => state.mallConfig.mall.setting,
        }),
        ...mapGetters('mallConfig', {
            getTheme: 'getTheme',
        }),
    },
    onReachBottom() {
        this.page++;
        this.getList();
    },

    methods: {
        navigator(str) {
            uni.navigateTo({
                url: str
            });
        },

        search() {
            let keyword = this.keyword;
            if (keyword === '') return;
            this.keyword = keyword.trim();
            this.reset();
        },
        reset() {
            this.page = 1;
            this.getGoodsList();
        },

        async getList() {
            const e = await this.$request({
                url: this.$api.pick.goods_list,
                method: 'get',
                data: {
                    keyword: this.keyword,
                    page: this.page,
                }
            });
            if (e.code === 0) {
                let { list} = e.data;
                this.list.push(...list);
            }
        },

        async getGoodsList() {
            const e = await this.$request({
                url: this.$api.pick.goods_list,
                method: 'get',
                data: {
                    keyword: this.keyword,
                    page: this.page,
                }
            });
            if (e.code === 0) {
                let { bg_url, list, form, activity } = e.data;
                this.isSearch = true;
                this.list = list;
                this.background = bg_url;
                this.form = form;
                this.activity = activity;
                this.setHistory();
            }
        },
        setHistory() {
            let historyList = this.getHistory();
            let keyword = this.keyword.trim();

            historyList.forEach((item, index) => {
                if (item.keyword === keyword) historyList.splice(index, 1);
            });
            historyList.push({
                keyword: keyword,
            });
            if (historyList.length > 20) historyList.shift();
            this.historyList = historyList;
            this.$storage.setStorageSync("SEARCH_HISTORY_LIST", historyList);
        },
        getHistory() {
            let historyList = this.$storage.getStorageSync('SEARCH_HISTORY_LIST');
            if (!historyList) historyList = [];
            return historyList;
        },

        deleteHistory() {
            this.$storage.removeStorageSync("SEARCH_HISTORY_LIST");
            this.historyList = [];
        },

        cancel() {
            this.isSearch = false;
            this.keyword = '';
            this.list = [];
        },

        historyClick(keyword) {
            this.keyword = keyword.trim();
            this.reset();
        },

        deleteKeyword() {
            this.keyword = '';
        }
    },

    watch: {
        keyword: {
            handler(v) {
                if (v == '') {
                    this.cancel();
                }
            },
            immediate: true,
        }
    },
}
